import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRouteSnapshot, Router, RouterStateSnapshot, UrlTree } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQueryCafe } from 'ionic-vhframeworks';
import { ImportKitchenA4Component } from 'src/app/cafe/components/print/import_kitchen/import-kitchen-a4/import-kitchen-a4.component';
import { ImportKitchenA5Component } from 'src/app/cafe/components/print/import_kitchen/import-kitchen-a5/import-kitchen-a5.component';
import { ImportKitchenK57Col3Component } from 'src/app/cafe/components/print/import_kitchen/import-kitchen-k57-col3/import-kitchen-k57-col3.component';
import { ImportKitchenK80Col3Component } from 'src/app/cafe/components/print/import_kitchen/import-kitchen-k80-col3/import-kitchen-k80-col3.component';
import { ImportKitchenK80Col4Component } from 'src/app/cafe/components/print/import_kitchen/import-kitchen-k80-col4/import-kitchen-k80-col4.component';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';

import { FunctionService } from 'src/app/cafe/services/function.service';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { VhBillType16Edit, VhBillType16DetailEdit, VhBillType16DetailAdd } from '../bill-type16-interface';
@Component({
  selector: 'bill-type16-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent {
   
  invoice: VhBillType16Edit =   this.router.getCurrentNavigation().extras.state.invoice
  invoice_detail: VhBillType16DetailAdd[] =  this.router.getCurrentNavigation().extras.state.invoiceDetail
  dataRestore: any = this.router.getCurrentNavigation().extras.state.dataRestore
  @Input() url = '';
  employee: any = {};
  pickerTime = new Date();
   
   
  printer: any;
  constructor(
    private router: Router,
    private vhComponent: VhComponent,
    private lang: LanguageService,
    private vhQueryCafe: VhQueryCafe,
    public vhAlgorithm: VhAlgorithm,
    private vhAuth: VhAuth,
    private fncService: FunctionService
  ) {
    this.employee = this.vhAuth.getlocalEmployee(this.invoice.id_employee); 
    // ------bắt sự kiện nhấn phím nóng-----
    this.eventKeypress();
  }
  ngOnInit(): void {
    let printer = this.vhQueryCafe.getLocalPrintPage('page_desktop_import_kitchen')
    this.printer = printer ? printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)] : { discount_hidden: false, note_hidden: false, fee_hidden: false, tax_hidden: false, debt_hidden: false };
    this.checkPrinter = this.printer.enable;



  }
  event: any // biến lắng nghe
  ngOnDestroy() {
    document.removeEventListener('keydown', this.event, false);
  }
  checkPrinter: boolean = false;
  eventKeypress() {
    this.event = (event: any) => {
      const keycode = event.keyCode;
      if (keycode === 112 || keycode === 113) event.preventDefault();
      if (keycode === 115) this.searchProductList = !this.searchProductList;
      else if (this.invoice_detail.length > 0) {
        switch (keycode) {
          case 112: this.payInvoice(16, false)
            break;
          case 113: {
            if (this.checkPrinter) this.payInvoice(16, true)
            break;
          }
        }
      }
    }
    document.addEventListener('keydown', this.event, false);
  }
  deleteGoods(detail: VhBillType16DetailAdd) {
    this.vhComponent.alertConfirm("", "", `${this.lang.translate("Delete")} ${detail.name}?`, "OK", this.lang.translate("Cancel"))
      .then(() => this.removeInvoiceDetail(detail.id_product), () => { })
  }
  searchProductList: boolean = false;
  openSearchProductList() {
    this.searchProductList = true;
  }
  closeSearchProductList(data) {
    if (data) {
      if (data.manylot && !data.lot_number) this.openModalChooseLot(data)
      else
        this.addInvoiceDetail(data);
    }

    this.searchProductList = false;
  }
  goBack() {
    this.vhComponent.alertConfirm(`${this.lang.translate("Confirm")}`, "", `${this.lang.translate("Bạn muốn thoát và không lưu hóa đơn này")} ?`, "OK", this.lang.translate('Cancel'))
      .then(() => { this.isExit = true; this.router.navigate([this.url], { state: this.dataRestore }) }, () => { })
  }
  checkQuantityProduct(product: VhBillType16DetailAdd, event) {
    if (!event) product.quantity = 1;
    ;
  }

  changeUnit(ev, item) {
    let unit = this.vhQueryCafe.getUnit_byRatio(item.units, ev);
    if (unit) {
      item.unit = unit.unit
      item.price = parseFloat(unit.price)
      item.ratio = parseFloat(unit.ratio)
      item.price_origin = parseFloat(unit.price)
    }
    
  }

  tempDetail: VhBillType16DetailAdd;
 
  // -------set employee-------
  closeAndReceiveEmployee(event) {
    if (event) {
      this.invoice.id_employee = (event);
    }
  }
  setPickerTime(event) {
    if (new Date(event).getTime() < new Date(this.vhAuth.getStore().registrationdate).getTime()) {
      this.vhComponent.alertMessageDesktop("warning", `${this.lang.translate("You can only create or edit the invoice from the registration date (")} ${this.fncService.formatDate(this.vhAuth.getStore().registrationdate)} ${")"}`, 5000)
      this.pickerTime = new Date(this.invoice.date)
      this.invoice.date = (new Date(this.pickerTime));
    }
    else this.vhQueryCafe.getValidityDate(new Date(event)).then((validitydate: any) => {
      if (validitydate == null) {
        this.invoice.date = (event);
      }
      else {
        this.pickerTime = new Date(validitydate)
        this.invoice.date = (new Date(this.pickerTime));
        this.vhComponent.alertMessageDesktop("warning", `${this.lang.translate("You can only create or edit the invoice from the date")} ${this.fncService.formatDate(validitydate)}`, 5000)
      }
    })
  }


  handle() {
    this.isExit = true;
    this.router.navigate([this.url], { state: this.dataRestore })
  }
  note: string;
  edit_note() {
    if (this.note) {
      this.invoice.note = (this.note)
    }
  }

  payInvoice(bill_type: 16, print?) {
    if (this.vhAuth.checkMyPermission("purchase_enable_payment")) {
      this.vhComponent.alertConfirm(
        `${this.lang.translate("Complete")} ?`,
        "", "", "OK", this.lang.translate("Cancel")
      ).then(() => {
        this.vhComponent.showLoading("", "transparent-loading").then(() => {
          var arrayProcess: any[] = [this.createOrUpdate(bill_type)];
          Promise.all(arrayProcess)
            .then((bill) => {
              this.vhComponent.hideLoading(0).then(() => {
                this.vhComponent.showToast(2000, `${this.lang.translate("Hóa đơn xuất hủy")} ${bill[0].bill_code || this.invoice.bill_code} ${this.lang.translate("has been updated successfully")}`, "success-toast")
                if (print) this.handlePrint()
                else this.handle()
              })
            })
        }).catch(err => { this.vhComponent.hideLoading(0) })
      }, () => { })
    }
    else this.vhComponent.showToast(1500, this.lang.translate('You do not have this rights'), "alert-toast")
  }
  createOrUpdate(bill_type: 16) {
    return this.vhQueryCafe.updateBill_Billdetail(this.invoice._id, this.invoice, this.getCreateUpdateInvoiceDetail())
  }

   /**
   * lấy thông tin dữ liệu trang in
   * @returns Promise<any>
   * @example this.checkPrint()
   */
  checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQueryCafe.getLocalPrintPage('page_desktop_import_kitchen')
      if (printer && printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable) resolve(printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)])
      else rejects("No")
    })
  }
  handlePrint() {
    this.checkPrint().then((printer) => {
      if (printer)
        this.vhComponent.showModal(this.renderPrintPage(printer['_id']), { printer: printer,invoice_detail : this.invoice_detail, invoice: this.invoice }, false, false, `modal-print-${printer['_id']}`).then((modal) => {
          modal.onWillDismiss().then(() => this.handle())
        })
    }, (err) =>
      this.vhComponent.alertMessage(this.lang.translate("Function"), "", this.lang.translate("Please turn on the printer function"), "OK")
        .then(() => this.handle())
    )
  }
  /**
   * trả về component của trang in tương ứng
   * @example this.renderPrintPage('print_size_a4')
   * @returns component
   */
  private renderPrintPage(type: 'print_size_a4' | 'print_size_a5' | 'print_size_k80_4c' | 'print_size_k80_3c' | 'print_size_k57_3c') {
    switch (type) {
      case 'print_size_k57_3c': return (ImportKitchenK57Col3Component)
      case 'print_size_k80_3c': return (ImportKitchenK80Col3Component)
      case 'print_size_k80_4c': return (ImportKitchenK80Col4Component)
      case 'print_size_a5': return (ImportKitchenA5Component)
      case 'print_size_a4': return (ImportKitchenA4Component)
      default: return (ImportKitchenA4Component)
    }
  }
  // ------thoát trang-----
  isExit: boolean = false; 
  show_modal_select_lots = false;
  list_search_lot = [];

  getLotNumber(lots, _id) {
    let lot = lots.find(item => item._id == _id);
    return lot.lot_number
  }
  searchLot(value) {
    this.tempDetail.lots = this.vhAlgorithm.searchList(this.vhAlgorithm.changeAlias(value), this.list_search_lot, ['lot_number'])
  }
  openModalChooseLot(data) {
    this.tempDetail = data
    this.list_search_lot = this.tempDetail.lots
    if (!this.list_search_lot.length) this.vhComponent.alertMessageDesktop("error", this.lang.translate("Sản phẩm này không có lô khả dụng để bán!"))
    else this.show_modal_select_lots = true;
  }

  changeLot(e) {
    this.tempDetail.lot_number = this.tempDetail.lots.find(item => item._id == e).lot_number
  }

  handleChooseLot() {
    console.log(this.list_search_lot);

    this.show_modal_select_lots = false;
    this.tempDetail.lots = this.list_search_lot;
    if (!this.tempDetail.id_product) this.closeSearchProductList(this.tempDetail);
  }

  handleCancelChooseLot() {
    this.show_modal_select_lots = false;
    this.tempDetail.lots = this.list_search_lot
  }

  /**Thêm sản phẩm vô bill detail */
  public addInvoiceDetail(value: any) {
    if (value['id_subproduct']) {
      if (value.lot_number) {
        let index = this.invoice_detail.findIndex(detail => detail['lot_number'] == value['lot_number'])
        if (index != -1) {
          if (this.invoice_detail[index]['quantity'] < 9999999999) this.invoice_detail[index]['quantity'] += 1
        } else {
          this.addBill_Detail(value)
        }
      }
      else {
        let index = this.invoice_detail.findIndex(detail => detail['id_subproduct'] == value['id_subproduct'] && detail['ratio'] == value['ratio'])
        if (index != -1) {
          if (this.invoice_detail[index]['quantity'] < 9999999999) this.invoice_detail[index]['quantity'] += 1
        } else {
          this.addBill_Detail(value)
        }
      }
    }
    else {
      if (value.lot_number) {
        let index = this.invoice_detail.findIndex(detail => detail['lot_number'] == value['lot_number'])
        if (index != -1) {
          if (this.invoice_detail[index]['quantity'] < 9999999999) this.invoice_detail[index]['quantity'] += 1
        } else {
          this.addBill_Detail(value)
        }
      }
      else {
        let index = this.invoice_detail.findIndex(detail => detail['id_product'] == value['_id'] && detail['ratio'] == value['ratio'])
        if (index != -1) {
          if (this.invoice_detail[index]['quantity'] < 9999999999) this.invoice_detail[index]['quantity'] += 1
        } else {
          this.addBill_Detail(value)
        }
      }
    }


  }

  addBill_Detail(value) {
    this.invoice_detail.unshift({
      name: value.name,
      id_product: value._id,
      quantity: 1,
      unit: value.unit,
      units: value.units,
      ratio: value.ratio ? value.ratio : 1,
      ptype: value.type,
      
      id_subproduct: value.id_subproduct,
      lots: value.lots,
      lot_number: value.lot_number,
      id_lotproduct: value.id_lotproduct,

    })

    
  }
  /**Thêm sản phẩm vô bill detail */
  public addInvoiceDetailMutilLine(value: any) {
    this.invoice_detail.push({
      name: value.name,
      id_product: value._id,
      quantity: 1,
      unit: value.unit,
      units: value.units,
      ratio: value.ratio ? value.ratio : 1,
      ptype: value.type,
     
      id_subproduct: value.id_subproduct,
      lots: value.lots,
      lot_number: value.lot_number,
      id_lotproduct: value.id_lotproduct,

    })

    

  }
  removeInvoiceDetail(id: string) {
    let index = this.invoice_detail.findIndex(detail => detail['id_product'] == id)
    if (index != -1) {
      this.invoice_detail.splice(index, 1);
      
    }
  } 
  public getCreateUpdateInvoiceDetail(): VhBillType16DetailAdd[] {
    return this.invoice_detail.map(item => {
      let detail: VhBillType16DetailAdd = {
        id_product: item.id_product,
       
        quantity: item.quantity,
        ptype: item.ptype,
        ratio: item.ratio ? item.ratio : 1,
      }
      if (item.id_lotproduct) detail['id_lotproduct'] = item.id_lotproduct
      if (item.id_subproduct) detail['id_subproduct'] = item.id_subproduct
      return detail
    })
  }
}
