
import { ChangeDetectorRef, Component, Input } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQueryCafe } from 'ionic-vhframeworks';
import { ImportKitchenA4Component } from 'src/app/cafe/components/print/import_kitchen/import-kitchen-a4/import-kitchen-a4.component';
import { ImportKitchenA5Component } from 'src/app/cafe/components/print/import_kitchen/import-kitchen-a5/import-kitchen-a5.component';
import { ImportKitchenK57Col3Component } from 'src/app/cafe/components/print/import_kitchen/import-kitchen-k57-col3/import-kitchen-k57-col3.component';
import { ImportKitchenK80Col3Component } from 'src/app/cafe/components/print/import_kitchen/import-kitchen-k80-col3/import-kitchen-k80-col3.component';
import { ImportKitchenK80Col4Component } from 'src/app/cafe/components/print/import_kitchen/import-kitchen-k80-col4/import-kitchen-k80-col4.component';

import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { FunctionService } from 'src/app/cafe/services/function.service';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { VhBillType16Edit, VhBillType16DetailEdit, } from '../bill-type16-interface';

@Component({
  selector: 'bill-type16-detail',
  templateUrl: './dettail.component.html',
  styleUrls: ['./dettail.component.scss']
})
export class DettailComponent {
  @Input() url = '';

  printer: any;
  tableHeight: string;
  public invoiceInfo: any;
  invoice_detail: VhBillType16DetailEdit[] = []
  public invoice: VhBillType16Edit
  dataRestore: any = this.router.getCurrentNavigation().extras.state.dataRestore


  constructor(
    private router: Router,
    private vhQueryCafe: VhQueryCafe,
    private lang: LanguageService,
    private vhAuth: VhAuth,
    private vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    private cdRef: ChangeDetectorRef,
    private fncService: FunctionService
  ) {
    this.getDataInvoice(this.router.getCurrentNavigation().extras.state.id)


  }

  /**
   * gán bill và bill-detail get được vào biến và get các thông tin của nv, khách hàng, phương thức thanh toán
   * @example this.init(bill, bill-deails)
   */
  init(invoice: VhBillType16Edit = null, invoiceDetail: VhBillType16DetailEdit[] = null) {
    console.log(invoice);

    this.invoice = invoice
    this.invoice_detail = invoiceDetail
    if (invoice && invoiceDetail) {
      this.invoiceInfo = {
        employee: this.vhAuth.getlocalEmployee(this.invoice.id_employee),
      }
    } else this.invoiceInfo = { employee: {} }

  }

  /**
   * get dữ liệu bill từ DB
   * @param id : nhận từ state truyền qua
   * @example this.getDataInvoice('DHIWHNXDIWODUHIWDHOWID')
   */
  private getDataInvoice(id) {
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      Promise.all([
        this.vhQueryCafe.getBill(id),
        this.vhQueryCafe.getBill_details_byId_bill(id)
      ]).then(([invoice, invoiceDetail]: any) => {
        if (!invoiceDetail.length)
          this.init(invoice, [])
        else {
          invoiceDetail = this.vhAlgorithm.sortVietnamesebyASC(invoiceDetail, 'name');
          this.init(invoice, invoiceDetail)
        }
        this.vhComponent.hideLoading(0)
      })
    })

  }


  /**
   * trả về tên unit của sản phẩm theo ratio truyền vào
   * @param units 
   * @param ratio 
   * @returns string
   * @example let unit_name = this.getUnit([object], 1)
   */
  getUnit(units: any[], ratio) {
    return this.vhQueryCafe.getUnit_byRatio(units, ratio).unit
  }

  /**
   * trả về chuỗi thông tin lô theo id lô truyền vào
   * @param lots 
   * @param _id 
   * @returns string
   * @example let lot = this.getLotNumber([], 'DNIWUHNDKLJWHDIWUOH')
   */
  getLotNumber(lots, _id) {
    let lot = lots.find(item => item._id == _id)
    return lot.lot_number + '\n' + this.fncService.formatDate(lot.date_mfg) + ' - ' + this.fncService.formatDate(lot.date_exp)
  }
  /**
   * xóa hóa đơn lên DB
   * @example this.deleteInvoice()
   */
  deleteInvoice() {
    if (this.vhAuth.checkMyPermission("entering_enable_cancel")) {
      this.vhComponent.alertConfirm(this.lang.translate("Confirm"), "", `${this.lang.translate("Delete")}?`, "OK", this.lang.translate("Cancel"))
        .then(() => {
          this.vhComponent.showLoading("", "transparent-loading").then(() => {
            this.vhQueryCafe.deleteBill_Billdetail(this.invoice._id).then(() => {
              this.vhComponent.hideLoading(0).then(() => {
                this.vhComponent.showToast(2000,
                  `${this.lang.translate("Delivery goods")} ${this.invoice.bill_code} ${this.lang.translate("has been cancelled successfully")}`, "success-toast")
                this.router.navigate([this.url])
              })
            })
          })
        }, () => { })
    }
    else this.vhComponent.showToast(1500, this.lang.translate('You do not have this rights'), "alert-toast")
  }
  public goToCart() {
    this.router.navigate([this.url + '/edit'], { state: { invoiceDetail: this.invoice_detail, invoice: this.invoice, dataRestore: this.dataRestore, } })
  }
  /**
  * lấy thông tin dữ liệu trang in
  * @returns Promise<any>
  * @example this.checkPrint()
  */
  checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQueryCafe.getLocalPrintPage('page_desktop_destroy_product')
      if (printer && printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable) resolve(printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)])
      else rejects("No")
    })
  }

  /**
   * kiểm tra thông tin dữ liệu trang in và mở component in
   * @example this.printInvoice()
   */
  printInvoice() {
    this.checkPrint().then((printer) => {
      if (printer)
        this.vhComponent.showModal(this.renderPrintPage(printer['_id']), { printer: printer, invoice_detail: this.invoice_detail, invoice: this.invoice }, false, false, `modal-print-${printer['_id']}`)
    }, (err) => {
      let state: any = "Xuất hủy hàng hóa"
      this.vhComponent.alertMessage(this.lang.translate("Function"), "", this.lang.translate("Please turn on the printer function"), "OK")
        .then(() => this.router.navigate(['/cafe/dashboard/management/printer', 'page_desktop_destroy_product'], { state: state }))
    })
  }

  /**
   * trả về component của trang in tương ứng
   * @example this.renderPrintPage('print_size_a4')
   * @returns component
   */
  private renderPrintPage(type: 'print_size_a4' | 'print_size_a5' | 'print_size_k80_4c' | 'print_size_k80_3c' | 'print_size_k57_3c') {
    switch (type) {
      case 'print_size_k57_3c': return (ImportKitchenK57Col3Component)
      case 'print_size_k80_3c': return (ImportKitchenK80Col3Component)
      case 'print_size_k80_4c': return (ImportKitchenK80Col4Component)
      case 'print_size_a5': return (ImportKitchenA5Component)
      case 'print_size_a4': return (ImportKitchenA4Component)
      default: return (ImportKitchenA4Component)
    }
  }
  goBack() {
    if (this.router.url == '/cafe/dashboard/management/invoice/ingredient/enter/detail')
      this.router.navigate(['/cafe/dashboard/management/invoice/ingredient'], { state: this.dataRestore });
    else
      this.router.navigate([this.url], { state: this.dataRestore });
  }
  ngAfterViewChecked() {
    if (document.querySelector("#delivery-good-detail-invoice") && document.querySelector(".ant-table-thead") && document.querySelector(".delivery-good-detail-invoice-header") && document.querySelector(".ant-table-pagination")) {
      this.tableHeight = document.querySelector("#delivery-good-detail-invoice").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".delivery-good-detail-invoice-header").clientHeight - document.querySelector(".ant-table-pagination").clientHeight - 40 + "px";
    }
    this.cdRef.detectChanges();
  }
}
